
#body-div {
	max-width: 746px;
	width: auto;
	height: auto;
	margin: 0 auto
}
.select-language {
	margin: 0 auto;
	width: 150px;
	padding: 20px 0 0 0
}
h1 {
	color: #CCCCCC;
	padding: 25px 0 10px;
	text-shadow: 1px 1px 1px #FFFFFF;
	width: 100%;
	text-align: center;
	margin: 0
}
h2 {
	color: #444;
	padding: 0 5px;
	line-height: 30px;
	border-bottom: 1px solid #e1dada;
	display: block;
	margin: 0 0 20px 0
}
h2 a {
	color: #3366FF
}
h2 a:hover {
	color: #0099ff
}
h3 {
	color: #999;
	font-size: 18px
}
.wrapper {
	width: 100%;
	background-color: #FBB56A;
	border: 2px solid #FFFFFF;
	box-shadow: 0 0 3px #AAAAAA;
	overflow: hidden;
	margin-top: 20px;
	display: block;
	-webkit-border-radius: 10px;
	-moz-border-radius: 10px;
	border-radius: 10px
}
.text-center {
	text-align: center
}
.wizard {
	width: 100%;
	min-height: 44px;
	height: auto;
	margin-bottom: 20px;
	display: block;
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	border-radius: 5px
}
.step1, .step2, .step3, .step4, .step5 {
	float: left;
	font-family: inherit;
	font-weight: bold
}
.step1, .step3 {
	width: 19%
}
.step4, .step5 {
	width: 17%
}
.step2 {
	width: 28%
}
.step1 {
	-webkit-border-radius: 5px 0 0 5px;
	-moz-border-radius: 5px 0 0 5px;
	border-radius: 5px 0 0 5px
}
.grayStep {
	background: url(../images/wizard/arrow.png) no-repeat 100% -132px, url(../images/wizard/bg.png) repeat-x 0 -89px
}
.grayLastStep {
	background: url(../images/wizard/arrow.png) no-repeat 100% -176px, url(../images/wizard/bg.png) repeat-x 0 -89px
}
.yellowStep {
	background: url(../images/wizard/arrow.png) no-repeat 100% -88px, url(../images/wizard/bg.png) repeat-x 0 -45px
}
.yellowLastStep {
	background: url(../images/wizard/arrow.png) no-repeat 100% -220px, url(../images/wizard/bg.png) repeat-x 0 -45px
}
.greenStep {
	background: url(../images/wizard/arrow.png) no-repeat 100% -44px, url(../images/wizard/bg.png) repeat-x 0 -1px
}
.greenStep2 {
	background: url(../images/wizard/arrow.png) no-repeat 100% 0, url(../images/wizard/bg.png) repeat-x 0 -1px
}
@media (min-width:978px) {
	.wizard {
		display: block
	}
	.step1, .step2, .step3, .step4, .step5 {
		float: left;
		line-height: 34px;
		font-size: 13px
	}
	.step1 p, .step2 p, .step3 p, .step4 p, .step5 p {
		float: left;
		padding: 6px 0 0 10px
	}
	.step1 img, .step2 img, .step3 img, .step4 img, .step5 img {
		width: auto;
		height: 24px;
		float: left;
		padding: 10px 0 0 5px
	}
	.step1, .step2, .step3, .step4, .step5 {
		max-height: 44px;
		height: auto;
		float: left
	}
	.step1, .step3 {
		width: 20%
	}
	.step4, .step5 {
		width: 17%
	}
	.step2 {
		width: 26%
	}
	.progress {
		display: none
	}
}
