button {
	border: none;
	outline: none;
	-moz-border-radius: 10px;
	-webkit-border-radius: 10px;
	border-radius: 10px;
	color: #ffffff;
	display: block;
	cursor: pointer;
	clear: both;
	padding: 8px 15px;
	text-shadow: 0 1px 1px #777;
	font-weight: bold;
	font-family: "Century Gothic", Helvetica, sans-serif;
	font-size: 18px;
	-moz-box-shadow: 0px 0px 3px #aaa;
	-webkit-box-shadow: 0px 0px 3px #aaa;
	box-shadow: 0px 0px 3px #aaa;
	background: #4797ED
}
button:hover {
	background: #d8d8d8;
	color: #666;
	text-shadow: 1px 1px 1px #fff
}
.input-small {
	width: 90px !important
}
.input-medium {
	width: 130px
}
.control-label {
	font-size: 13px;
	font-weight: bold;
	text-align: left !important
}
.fl {
	float: left
}
.fr {
	float: right
}
.progress {
	display: none
}
.normal-table tr td {
	padding: 4px 10px
}
.htitel {
	width: 98%;
	padding: 0 1%;
	min-height: 35px;
	height: auto;
	border-bottom: 2px solid #e1dada;
	margin-top: 5px;
	display: inline-block
}
.back1, .continue1, .continue2, .home {
	margin: 2px 0
}
.search-gallery, .search-details {
	display: block
}
.table2 tr td {
	padding: 2px 5px
}
.table3 {
	width: 100%;
	background-color: #fac48b
}
.table3 tr td.al-r {
	text-align: right
}
